import string from './style.js'

const demo = document.querySelector('#demo')
const demo2 = document.querySelector('#demo2')

// Âú® demo ÈáåÂ±ïÁ§∫Âá∫ÊñáÊú¨ÂÜÖÂÆπÔºåÂêåÊó∂ÔºåÂú® demo2 ÈáåÂêåÊ≠•Âä†‰∏äÊ†∑Âºè
let n = 1
demo.innerText = string.substr(0, n)
demo2.innerHTML = string.substr(0, n)

// ÊØèÈöî‰∏ÄÊÆµÊó∂Èó¥ÔºåÊâßË°å setInterval ÈáåÈù¢ÁöÑÂáΩÊï∞ÔºåÂπ∂ËÆ∞ÂΩïÊØèÊ¨°ÊâßË°åÂÆåÂêéËøîÂõûÁöÑ id
// ÂΩì n Â§ß‰∫éÂ≠óÁ¨¶‰∏≤ÈïøÂ∫¶Êó∂ÊöÇÂÅú
let time = 100
const run = () => {
  n += 1
  if (n > string.length) {
    window.clearInterval(id)
    return
  }
  demo.innerText = string.substr(0, n)
  demo2.innerHTML = string.substr(0, n)
  demo.scrollTop = demo.scrollHeight // ÊªöÂä®Êù°Ê†πÊçÆÈ°µÈù¢Âá∫Áé∞ÁöÑÂÜÖÂÆπËá™Âä®ÂæÄ‰∏ãÊªö
}
const play = () => {
  return setInterval(run, time) // setInterval Êé•ÂèóÁöÑÁ¨¨‰∏Ä‰∏™ÂèÇÊï∞ÊòØÂáΩÊï∞ÔºåÊâÄ‰ª• run ÂêéÈù¢‰∏çË¶ÅÂä†Êã¨Âè∑
}
const pause = () => {
  window.clearInterval(id)
}
let id = play() // id Âè™ËÉΩÊîæÂú®ÂÖ®Â±ÄÔºåÂõ†‰∏∫ play Âíå pause ÈÉΩ‰ºöÁî®Âà∞ id

document.querySelector('#btnPause').onclick = () => {
  pause()
}
document.querySelector('#btnPlay').onclick = () => {
  pause()
  id = play()
}
document.querySelector('#btnSlow').onclick = () => {
  pause()
  time = 300
  id = play()
}
document.querySelector('#btnNormal').onclick = () => {
  pause()
  time = 100
  id = play()
}
document.querySelector('#btnFast').onclick = () => {
  pause()
  time = 0
  id = play()
}

